import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Container, Row, Col, Button, Form, FormGroup, FormLabel, Image } from 'react-bootstrap';
import Api from '../Api';

const toBase64 = file =>
	new Promise((resolve, reject) => {
		const reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => resolve(reader.result);
		reader.onerror = error => reject(error);
	});

function Register({ onLogin }) {
	const [form, setForm] = useState({});
	const history = useHistory();

	async function onChange(e) {
		const { name, value, files } = e.target;

		// handle img  file input
		if (name == 'profilepPic') {
			let newImages = '';
			if (files) {
				// console.log(files)
				//  convert it to base64
				newImages = await toBase64(files[0]);
			}
			//preview img
			const showImgSection = document.querySelector('#profileImg');
			showImgSection.classList.remove('invisible');
			// update state with new files of base64
			return setForm({ ...form, [name]: newImages });
		}

		// handle other  input's
		setForm({ ...form, [name]: value });
		console.log(form);
	}

	async function onSubmit(e) {
		e.preventDefault();
		console.log('form:', form);

		// send request to server register new user
		const data = await Api.addNewUser(form)
		console.log('from server', data);

		// save token local storage for next api calls
		localStorage.setItem('token', data.token);
		// update use state in App Component
		onLogin(data.user);

		// redirect to homePage
		history.push('/');
	}

	return (
		<Container>
			<Form onChange={onChange} onSubmit={onSubmit} dir="rtl">
				<Container fluid={true} className={'border-right border-left px-5'} style={{ maxWidth: '430px' }}>
					<Form.Row>
						<FormGroup as={Col} className="text-center">
							<FormLabel> :</FormLabel>
							<Form.Control size="sm" type="text" name="firstName" placeholder="  " minlength="2" required />
						</FormGroup>
						<FormGroup as={Col} className=" text-center">
							<FormLabel> :</FormLabel>
							<Form.Control size="sm" type="text" name="lastName" placeholder="  " minlength="2" required />
						</FormGroup>
					</Form.Row>
					<FormGroup as={Row} className="text-right">
						<FormLabel> :</FormLabel>
						<Form.Control size="sm" type="email" name="email" placeholder="  " required />
					</FormGroup>
					<FormGroup as={Row} className="text-right">
						<FormLabel>:</FormLabel>
						<Form.Control size="sm" type="password" name="password" placeholder="  " required />
					</FormGroup>
					<FormGroup as={Row} className="text-right">
						<FormLabel> :</FormLabel>
						<Form.Control
							size="sm"
							type="tel"
							name="phone"
							pattern="[0-9]{3}-[0-9]{7}"
							placeholder="  888-8888888"
							required
						/>
					</FormGroup>
					<FormGroup as={Row} className="text-right">
						<FormLabel> </FormLabel>
						<Form.Control size="sm" type="text" name="city" placeholder="   " minlength="2" required />
					</FormGroup>
					<FormGroup as={Row} className="image">
						<FormLabel>  </FormLabel>
						<Form.File name="profilepPic" />
					</FormGroup>
					<Row className="justify-content-center invisible" id="profileImg">
						<Image width="170" height="180" src={form.profilepPic} alt="prev" roundedCircle />
					</Row>

					<Button type="submit" variant="primary" size="md" block>
						
					</Button>
				</Container>
			</Form>
		</Container>
	);
}

export default Register;
